import React, { Component } from 'react';

import BlogItem from './BlogItem';
import './Blog.css';

class Blog extends Component {
	renderPosts() {
		// console.log(new Date("2018-12-31").getTime());
		const postFeed = this.props.posts ? this.props.posts.map((post, ind) => {
      		return(<BlogItem post={ post } index={ "post-" + ind } filtered={ this.props.filtered } />);
      	})
		// .sort((postA, postB) => {
  //     		console.log(new Date(postA.props.post.fields.dateCreated).getTime());
  //     		return (new Date(postA.props.post.fields.dateCreated).getTime()) - (new Date(postB.props.post.fields.dateCreated).getTime());
  //     	}) 
		: null;
      	return postFeed;
	}

	render() {
		return (
		  <div className="blog">
		      {
		      	this.renderPosts()
		      } 
		  </div>
		);
	}
}

export default Blog;



