import React, { Component } from 'react';
import './Menu.css';

class Menu extends Component {
	constructor() {
		super();

		this.state = {
			categories: [],
			selectedCategory: ""
		}
	}

	selectFilter(category) {
		let paramCategory;
		if (this.state.selectedCategory === category) {
			paramCategory = "";
		} else  {
			paramCategory = category;
		}
		this.setState({ selectedCategory: paramCategory}, function() {
			this.props.filterPosts(this.state.selectedCategory);
		});
	}


	renderCategories() {
		const categories = [];
		if (this.props.posts) {
			this.props.posts.forEach((post, index, arr) => {
				if (categories.indexOf(post.fields.category) < 0) {
					categories.push(post.fields.category);
				}
			})
		}
		const categoriesRender = categories.sort().reverse().map((category) => {
			return (
				<li>
					<a href={ this.props.page === 'info' ? `${process.env.PUBLIC_URL}/` : `${process.env.PUBLIC_URL}/#` } className={ this.state.selectedCategory === category ? "selected" : "" } onClick={ () => this.selectFilter(category) } >
						{ category }
					</a>
				</li>)
			;
		});
		return categoriesRender;
	}

	render() {
		return (
		  <div id="menu" className={ this.props.filtered ? "menu " + this.props.filtered.replace(" ", "-") : "menu" }>
		  	<header>
			  	<a href={ this.props.page === 'info' ? `${process.env.PUBLIC_URL}/` : `${process.env.PUBLIC_URL}/info` }><img alt="logo" src={`${process.env.PUBLIC_URL}/images/logo.gif`} /></a>
			  	<div className="links">
			  		<ul>
			  			{
			  				this.renderCategories()
			  			}
			  			<li><a href={`${process.env.PUBLIC_URL}/info`}>?</a></li>
			  		</ul>
			  	</div>
			  	<div className="categories">
			  	</div>
		   	</header>
		  </div>
		);
	}
}

export default Menu;



