import React, { Component } from 'react';
import * as Markdown from 'react-markdown';
import './Commercial.css';

class BlogItem extends Component {
	constructor() {
		super();
		this.state = {
			show: true,
      img1Factor: false,
      img2Factor: false
		}
	}

  componentDidMount() {
    let img1 = document.getElementById(this.props.index + "-img1");
    let img2 = document.getElementById(this.props.index + "-img2");



    if (img1) {
      img1.addEventListener("load", () => {
        console.log("Woof: " + img1.width);
        if (img1.width < 345) this.setState({ img1Factor: true });
      })
    }

    if (img2) {
      img2.addEventListener("load", () => {
        if (img2.width < 345) this.setState({ img2Factor: true });
      })
    }
    
    document.querySelector("iframe").onload = setTimeout(this.removeLoading(), 4000);
    

    // if (img1.width < 345) img1.style.transform = `scale(345 / ${ img1.width })`;
    // if (img2.width < 345) img2.style.transform = `scale(345 / ${ img2.width })`;
  }

	componentDidUpdate(prevProps) {
		if (prevProps.filtered !== this.props.filtered) {
			this.toggleShowHide();
		}
	}

  removeLoading() {
    let loading = document.getElementById("loading-container")
    loading.classList.add('hide');
  }
	
	toggleShowHide() {
		const category = this.props.post.fields.category;
		if (this.props.filtered === "" || this.props.filtered === category) this.setState({ show: true });
		else this.setState({ show: false });
	}


	render() {
		let videoId = this.props.post.fields.videoId;
  		let title = this.props.post.fields.title;
  		let client = this.props.post.fields.client;
  		let year = this.props.post.fields.dateCreated && this.props.post.fields.dateCreated.toString().split("-")[0];
  		let description = this.props.post.fields.description;
  		let clientUrl = this.props.post.fields.clientUrl;
  		
		return (
          this.props.post.fields.category !== 'personal' ?
      		<div className={!this.state.show ? "post-body hidden" : !videoId || videoId.length < 1 ? "post-body no-video" : !this.props.post.fields.imageOne && !this.props.post.fields.imageTwo ? "post-body no-image" : "post-body"}>

      			{ videoId && videoId.length > 0 ?
              <div className="video">
        				<div style={{padding:'56.25% 0 0 0',position:'relative'}}><iframe onload={ this.removeLoading }title={ "video-" + videoId } src={'https://player.vimeo.com/video/' + videoId} style={{position:'absolute',top:'0',left:'0',width:'100%',height:'100%'}} frameborder="0" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe></div><script src="https://player.vimeo.com/api/player.js"></script>
        			</div> :
              null
            }

            { !this.props.post.fields.imageOne && !this.props.post.fields.imageTwo ?
              null :
        			<div className="post-content">
        				<div className="image-row">
                { 
                  this.props.post.fields.imageOne && this.props.post.fields.imageOne.fields.file.url ? 
  	      				<div className="image-frame imageOne"><img /*style={ this.state.img1Factor ?  { width: '345px' } : null }*/ id={ this.props.index + "-img1" } className={ this.props.index + "-img" } alt="img1" src={this.props.post.fields.imageOne.fields.file.url} /></div> :
                  null
                }
                {
                  this.props.post.fields.imageTwo && this.props.post.fields.imageTwo.fields.file.url ? 
  	      				<div className="image-frame imageTwo"><img /*style={ this.state.img2Factor ?  { width: '345px' } : null } */ id={ this.props.index + "-img2" } className={ this.props.index + "-img" } alt="img2" src={this.props.post.fields.imageTwo.fields.file.url} /></div> :
                  null
                }
        				</div>
        			</div>
            }
      			<div className="post-info">
      				<div className="project-title">
      					{
      						(clientUrl && clientUrl !== "" ?
      						<div><a href={clientUrl} className="client">{client}:</a> {title} ({year})</div> :
      						<div>{client}: {title} ({year})</div>)
      					}
      				</div>
      				<div className="project-description">
                <Markdown source={ description } />
      				</div>
      			</div>
      		</div> : 

          <div className={this.state.show ? "post-body personal" : "post-body hidden"}>

            <div className="post-content personal">
              <div className="image-row">
                { this.props.post.fields.imageOne && this.props.post.fields.imageOne.fields.file.url ? 
                  <div className="image-frame imageOne"><img alt="img1" src={this.props.post.fields.imageOne.fields.file.url} /></div> :
                  null 
                }
                { this.props.post.fields.imageTwo && this.props.post.fields.imageTwo.fields.file.url ? 
                  <div className="image-frame imageTwo"><img alt="img2" src={this.props.post.fields.imageTwo.fields.file.url} /></div> :
                  null
                }
              </div>
            </div>
            <div className="post-info">
              <div className="project-title">
                {
                  <div>{title} ({year})</div>
                }
              </div>
              <div className="project-description">
                <Markdown source={ description } />
              </div>
            </div>
          </div>

		);
	}
}

export default BlogItem;



