import React, { Component } from 'react';
import { withRouter} from 'react-router-dom';
import '../App.css';
import BlogStream from './Blog';
import Menu from './Menu';
import * as contentful from 'contentful';

class Home extends Component {
	constructor() {
		super();

		this.state = {
			filtered: ""
		}

		this.client = contentful.createClient({
		  	space: 'kqppwwuu42ep',
		  	accessToken: '54e2fe90df005a1109ff8c3f5c3502a6ff8cce7e8eb24b184bc16806fdbcb163',
		  	posts: []
		});

	}

	componentWillMount() {
	    this.fetchPosts();
	}

	fetchPosts() {
		this.client.getEntries({
			content_type: 'portfolio',
			order: '-fields.dateCreated'
		})
		.then(entries => {
			console.log(entries);
			this.setState({ posts: entries.items, entries: entries });
		});
	}

	filterPosts(category) {
		this.setState({ filtered: category }, ()=> {
			if (category === "creative direction") document.body.style.backgroundColor = "blue";
			else if (category === "art") document.body.style.backgroundColor = "red";
			else document.body.style.removeProperty("background-color");
		});
	}

	render() {
		return (
			<div className={"App " + this.state.filtered.replace(" ", "-") }>
				<Menu 
					filterPosts={ this.filterPosts.bind(this) } 
					posts={ this.state.posts }
					page="home"
					filtered={ this.state.filtered  }
				/>
				<BlogStream 
					filtered={ this.state.filtered  }
					posts={ this.state.posts }
				/>
			</div>
		);
	}
}

export default withRouter(Home);



